.iconsPanel {
    @apply flex justify-between max-w-[438px] bg-white rounded-lg shadow-popUp mt-8 mobile:mt-6 pt-3 mobile:pt-2 px-4 mobile:px-2 pb-5 mobile:pb-[14px] mobile:w-full mobile:mb-10;

    .item {
        @apply min-h-[95px] mobile:min-h-[76px] text-center px-2;

        svg {
            @apply w-12 h-12 mb-2 mx-auto mobile:w-8 mobile:h-8;
        }

        h4,
        span {
            @apply text-[#1E282D] static;
        }

        h4 {
            @apply mb-0 text-base mobile:text-sm font-semibold mobile:-mb-1;
        }

        span {
            @apply text-[10px] inline-block font-medium;
        }
    }
}
