.stage {
    @apply flex w-full relative mobile:mx-auto;

    & + .stage {
        @apply mt-[60px] top-0;
    }

    .stageAside {
        @apply relative mr-8 mobile:mr-0 before:block before:absolute before:top-32 before:bottom-0 before:left-0 before:right-0 before:mx-auto before:w-[2px] before:bg-[#1E282D] before:rounded-sm mobile:before:content-none mobile:absolute mobile:top-0 mobile:left-0;
    }

    .stageHeading {
        @apply flex justify-center items-center w-20 mobile:w-[68px] h-20 mobile:h-[68px] border border-[#1E282D] bg-[#2A3266]  rounded-full;

        span {
            @apply text-[40px] text-white;
        }

        path {
            @apply fill-white stroke-white;
        }
    }

    .halign {
        @apply flex relative w-full mobile:flex-col;

        .stageHeading {
            @apply absolute;
        }

        img {
            @apply notMobile:mt-auto ml-auto mobile:mr-auto;
        }
    }

    h3 {
        @apply text-[34px] font-bold font-secondary mb-2 mobile:text-xl mobile:pl-[92px] mobile:mb-[34px] mobile:pt-5;
    }

    p,
    ul {
        @apply mobile:text-sm;
    }

    p + p,
    p + ul {
        @apply mt-10 mobile:mt-6;
    }
}
