.bannerData {
    @apply flex items-center w-full max-w-[1100px] absolute bottom-0 mx-auto z-10 text-white mobile:text-right mobile:px-4;

    h2,
    h3 {
        @apply font-secondary;
    }

    h2 {
        @apply text-[52px] leading-[60px] mobile:text-xl;
    }

    h3 {
        @apply text-[40px] leading-[46px] mobile:leading-[23px] mobile:text-xl;
    }

    p {
        @apply text-xl mt-6 mobile:mt-2 mb-10 mobile:mb-4 font-medium mobile:text-sm;
    }

    svg {
        @apply mobile:w-[153px] static;
    }
}
